body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.link {
  color: #fff;
  background-color: #db7093;
  border: none;
  border-radius: 5px;
  margin: 15px 0 0 15px;
  padding: 10px 15px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: #000;
  background-color: #00f;
}

.form-search {
  justify-content: center;
  margin-top: 50px;
  display: flex;
}

.form-input {
  border: 2px solid tomato;
  border-radius: 5px 0 0 5px;
  padding: 15px 20px;
  font-size: 16px;
}

.form-select {
  border: 2px solid tomato;
  border-left: 0;
  padding: 10px;
  font-size: 16px;
}

.form-button {
  color: #fff;
  cursor: pointer;
  background-color: tomato;
  border: 2px solid tomato;
  border-radius: 0 5px 5px 0;
  padding: 10px 15px;
  font-size: 16px;
}

.form-button:hover {
  background-color: #00f;
  border: 2px solid #00f;
}

.list-weather {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 50px;
  display: flex;
}

.item-weather {
  box-sizing: border-box;
  width: calc(33.3% - 20px);
  text-align: center;
  border-radius: 5px;
  margin: 0 10px 10px 0;
  padding: 5px;
  list-style-type: none;
}

